@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: local("Oswald Regular"), local("Oswald-Regular"), url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: local("Oswald Medium"), local("Oswald-Medium"), url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
html {
  box-sizing: border-box;
}
*,
*::before,
*::after {
  box-sizing: inherit;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  color: #444444;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  line-height: 18px;
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0 0 0 0);
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
}
.container {
  width: 320px;
  margin: 0 auto;
  padding-right: 20px;
  padding-left: 20px;
}
.button {
  display: inline-block;
  padding-top: 11px;
  padding-bottom: 11px;
  border: none;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-decoration: none;
  text-transform: uppercase;
  vertical-align: center;
  cursor: pointer;
}
.accessible:focus {
  outline: 2px auto #0096ff;
}
@media (min-width: 768px) {
  .container {
    width: 768px;
    padding-right: 30px;
    padding-left: 30px;
  }
}
@media (min-width: 1300px) {
  .container {
    width: 1440px;
    padding-right: 110px;
    padding-left: 110px;
  }
}
.instance {
  background-color: #f2f2f2;
}
.instance__title {
  margin: 0;
  padding-top: 17px;
  padding-bottom: 10px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 60px;
}
.instance__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.instance__text {
  padding-right: 14px;
  padding-bottom: 10px;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}
.instance__value {
  display: inline-block;
  position: relative;
  width: 124px;
  height: 56px;
  margin: 0;
  padding-top: 13px;
  padding-bottom: 17px;
  border: 1px solid #cfcfcf;
  border-radius: 1px;
  background-color: #f2f2f2;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  text-align: center;
  text-transform: uppercase;
  vertical-align: middle;
}
.instance__data {
  display: inline-block;
  position: absolute;
  bottom: -16px;
  left: 19px;
  width: 83px;
  padding-right: 14px;
  padding-left: 14px;
  background-color: #f2f2f2;
  font-family: "Arial", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 12px;
  text-align: center;
  text-transform: none;
}
.instance__sum {
  margin-top: 34px;
  margin-right: 40px;
  margin-bottom: 37px;
  margin-left: 47px;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
}
.instance__column-slider {
  padding-right: 0;
  padding-left: 0;
}
.instance__slider-wrapper {
  display: flex;
  position: relative;
  height: auto;
  margin-top: 0;
  margin-right: auto;
  margin-bottom: 30px;
  margin-left: auto;
  text-align: center;
}
.instance__photo-wrapper {
  width: 100%;
}
.instance__photo-wrapper--fat-cat {
  flex-shrink: 0;
}
.instance__photo-wrapper--fat-cat .instance__image {
  object-fit: cover;
  object-position: left;
  font-family: "object-fit: cover; object-position: left;", sans-serif;
}
.instance__photo-wrapper--slim-cat {
  flex-shrink: 2;
}
.instance__photo-wrapper--slim-cat .instance__image {
  object-fit: cover;
  object-position: right;
  font-family: "object-fit: cover; object-position: right;", sans-serif;
}
.instance__photo-wrapper--appearance {
  animation: appearance 0.7s;
}
.instance__photo-wrapper--disappearance {
  animation: disappearance 0.7s;
}
.instance__image {
  width: 100%;
  max-width: none;
  height: 235px;
  object-fit: cover;
}
.instance__range {
  width: 200px;
  padding-right: 66px;
  padding-bottom: 35px;
  padding-left: 64px;
}
