.filter__list {
  margin: 0;
  padding: 0;
  padding-top: 26px;
  list-style: none;
}
.filter__label--radio {
  position: relative;
  padding-left: 22px;
  color: #222222;
  cursor: pointer;
}
.filter__label--radio::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -24px;
  width: 30px;
  height: 30px;
  border: 1px solid #cfcfcf;
  border-radius: 50%;
  background-color: #ffffff;
}
.filter__label--radio:hover {
  color: #222222;
}
.filter__label--radio:hover::before {
  border-color: #bababa;
}
.filter__label--radio:active {
  opacity: 0.3;
}
.filter__input--radio:checked + .filter__label--radio::after {
  content: "";
  position: absolute;
  top: 6px;
  left: -15px;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #68b738;
}
.filter__option {
  margin-bottom: 26px;
}
.filter__option:nth-child(2) {
  margin-bottom: 23px;
}
.filter__option--extra {
  margin-bottom: 24px;
}
.filter__input {
  opacity: 0;
  cursor: pointer;
}
.filter__list-extra {
  padding-right: 20px;
  padding-left: 22px;
}
.filter__label--checkbox {
  position: relative;
  padding-left: 20px;
  color: #222222;
}
.filter__label--checkbox::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -26px;
  width: 30px;
  height: 30px;
  border: 1px solid #cfcfcf;
  background-color: #ffffff;
}
.filter__label--checkbox:hover {
  color: #222222;
}
.filter__label--checkbox:hover::before {
  border-color: #bababa;
}
.filter__label--checkbox:active {
  opacity: 0.3;
}
.filter__input--checkbox:checked + .filter__label--checkbox::after {
  content: "";
  position: absolute;
  top: 4px;
  left: -20px;
  width: 20px;
  height: 12px;
  transform: rotate(-45deg);
  border-bottom: 3px solid #68b738;
  border-left: 3px solid #68b738;
}
.filter__input:focus + label::before {
  box-shadow: 0 0 4px 2px #4d90fe;
}
