.benefits {
  padding-top: 17px;
}

.benefits__title {
  margin-top: 0;
  margin-bottom: 30px;
  color: #111111;
  font-family: @font-headings;
  font-size: 36px;
  font-weight: 400;
  line-height: 60px;
}

.benefits__list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;
}

.benefits__item {
  position: relative;
  margin-bottom: 26px;
  padding-left: 81px;
  color: #444444;
  font-family: @font-general;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;

  &:last-child {
    margin-bottom: 46px;
  }

  &::after {
    content: "";
    position: absolute;
    z-index: 1;
    top: -2px;
    left: 0;
    width: 60px;
    height: 60px;
    background-color: @green;
    background-repeat: no-repeat;
  }

  &:first-child::after {
    background-image: url("../img/advantage-leaf@1x.png");
    background-position: 5px 5px;
  }

  &:nth-child(2)::after {
    background-image: url("../img/advantage-powder@1x.png");
    background-position: 5px 4px;
  }

  &:nth-child(3)::after {
    background-image: url("../img/advantage-dining@1x.png");
    background-position: 4px 5px;
  }

  &:nth-child(4)::after {
    background-image: url("../img/advantage-clock@1x.png");
    background-position: 5px 5px;
  }
}

@media (min-width: @tablet-width) {
  .benefits {
    padding-top: 46px;
  }

  .benefits__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin-left: -60px;
    counter-reset: benefits__item;
  }

  .benefits__title {
    font-size: 60px;
    line-height: 60px;
  }

  .benefits__item {
    position: relative;
    width: 324px;
    margin-bottom: 90px;
    margin-left: 60px;
    padding-top: 155px;
    padding-right: 65px;
    padding-left: 0;
    font-size: 16px;
    line-height: 24px;

    &:last-child {
      margin-bottom: 100px;
    }

    &::before {
      content: counter(benefits__item) "";
      position: absolute;
      z-index: -1;
      color: #f2f2f2;
      font-family: @font-headings;
      font-size: 281px;
      font-weight: 400;
      line-height: 281px;
      counter-increment: benefits__item;
    }

    &:first-child::before {
      top: 2px;
      left: 118px;
    }

    &:nth-child(2)::before {
      top: 3px;
      left: 111px;
    }

    &:nth-child(3)::before {
      top: 4px;
      left: 113px;
    }

    &:nth-child(4)::before {
      top: 3px;
      left: 114px;
    }

    &::after {
      top: 41px;
      left: 1px;
      width: 80px;
      height: 80px;
    }

    &:first-child::after {
      background-position: 15px 15px;
    }

    &:nth-child(2)::after {
      background-position: 15px 14px;
    }

    &:nth-child(3)::after {
      background-position: 14px 15px;
    }

    &:nth-child(4)::after {
      background-position: 15px 15px;
    }
  }
}

@media (min-width: @desktop-width) {
  .benefits {
    padding-top: 73px;
  }

  .benefits__list {
    margin-left: -80px;
  }

  .benefits__item {
    width: 245px;
    min-height: 290px;
    margin-bottom: 0;
    margin-left: 80px;
    padding-right: 0;

    &:last-child {
      margin-bottom: 0;
    }
  }
}
